@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
:root {
  --darkest: #221a0f;
  --gray: #414042;
  --font: "Lato";
  --off-white: #f9f9f9;
}

/* to fix jumpy scrollbar */
/* Commented out because it messes up the html2canvas export */
/* html {
  margin-left: calc(100vw - 100%);
  margin-right: 0;
} */

body {
  font-family: "Lato";
  font-family: var(--font);
  background-color: white;
}

.app-header {
  display: flex;
  justify-content: center;
}

.app-version {
  color: #414042;
  color: var(--gray);
  font-size: 0.7em;
}

.preview-download {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.preview-download-help {
  width: 30%;
}

.container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}

.interface {
  color: #221a0f;
  color: var(--darkest);
  /* height         : 80vh; */
  display: flex;
  /* align-items       : center; */
  flex-direction: column;
  justify-content: space-around;
  width: 720px;
  margin-left: 25px;
  margin-right: 25px;
}

button,
select {
  width: 190px;
  font-size: 100%;
  font-family: inherit;
  border: 0;
  padding: 5px;
  margin: 5px;
  background: white;
  border: 2px solid #414042;
  border: 2px solid var(--gray);
  font-weight: bold;
  text-transform: uppercase;
  transition: background 0.2s ease-in-out, color 0.2s ease-in-out;
}

label {
  margin-left: 5px;
}

button:hover,
button.selected {
  color: white;
  background-color: #414042;
  background-color: var(--gray);
}

.asset-selection {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.asset-selection-controls {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-top: -5px;
}

.asset-choice {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: 0.2s ease-in-out;
  margin: 10px;
}

.asset-choice:hover {
  transform: scale(1.15, 1.15);
  z-index: 100;
}

.asset-choice .asset {
  background-image: url(/ironsworn-asset-workbench/static/media/ironsworn_asset_background_rounded.a1f6e0f1.svg);
}

.add-new-asset.asset {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Lato";
  font-family: var(--font);
}

.add-new-asset-text {
  font-size: 3em;
  width: 75%;
  text-align: center;
  line-height: normal;
}

.interface-input {
  height: 90%;
  width: 100%;
  min-height: 500px;
  min-width: 400px;
}

.json-error-container {
  height: 100px;
}

.json-error {
  height: 100%;
  margin-left: 5px;
  display: flex;
  align-items: center;
}

.json-error button {
  width: 400px;
}

.editor {
  display: flex;
  flex-direction: column;
  border: 5px solid #414042;
  border: 5px solid var(--gray);
  box-sizing: border-box;
  width: 100%;
  min-height: 300px;
  background-color: #f9f9f9;
  background-color: var(--off-white);
}

.editor-view {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.text-input,
.number-input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 10px;
}

input,
textarea {
  font-family: "Lato";
  font-family: var(--font);
  font-size: 16px;
}

.text-input input,
.number-input input,
.text-input textarea {
  margin-left: 5px;
}

.text-input input {
  width: 200px;
}

.number-input input {
  width: 100px;
}

.font-inputs {
  display: flex;
}

.font-inputs .text-input input {
  width: 350px;
}

.thin-box-border {
  border: 1px solid #414042;
  border: 1px solid var(--gray);
  margin: 5px;
  padding: 5px;
}

.vertical {
  display: flex;
  flex-direction: column;
}

.horizontal {
  display: flex;
  justify-content: space-between;
}

.view-switcher {
  display: flex;
  justify-content: space-between;
}

.change-view {
  width: 100%;
  margin: 0;
  border: none;
  background-color: #414042;
  background-color: var(--gray);
  color: #f9f9f9;
  color: var(--off-white);
}

.change-view.selected,
.change-view:hover {
  background-color: #f9f9f9;
  background-color: var(--off-white);
  color: #414042;
  color: var(--gray);
}

.ability-input {
  margin: 10px 10px 20px 10px;
  padding: 5px;
  border-left: 4px solid #414042;
  border-left: 4px solid var(--gray);
  border-radius: 4px;
}

.ability-input-top,
.ability-input-top-fields,
.ability-input-top-fields .text-input {
  display: flex;
  flex-direction: row;
}

.ability-input-top {
  justify-content: space-between;
}

.ability-button {
  width: 100px;
  border: 1px solid #414042;
  border: 1px solid var(--gray);
  margin: 0;
  margin-left: 5px;
}

.icon-import {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.icon-import-helptext {
  margin-left: 5px;
  width: 80%;
}

.icon-import-author,
.icon-import-fileselect {
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
}

.icon-import-author input {
  width: 200px;
}

.icon-import-author input,
.icon-import-fileselect input {
  margin-left: 5px;
}

.help {
  display: flex;
  justify-content: center;
}

.bottom-controls {
  /* border      : 3px solid var(--gray); */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.export {
  height: 100%;
  display: flex;
  align-items: center;
}

.scale-buttons {
  display: flex;
  flex-wrap: wrap;
}

.footer {
  text-align: center;
  color: #777;
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.footer a,
.footer a:visited {
  color: blue;
  text-decoration: none;
}

.footer p {
  max-width: 570px;
  margin: 0 15px 10px 15px;
}

.credits {
  font-size: 0.9em;
  max-width: 800px;
}

.assets {
  display: flex;
  max-width: 2250px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-left: 25px;
  margin-right: 25px;
  margin-bottom: 20px;
}

.sheet-of-assets {
  display: flex;
  flex-wrap: wrap;
  width: 2250px;
}

:root {
  --darkest: #221a0f;
  --gray: #414042;
  --asset-width: 750px;
  --asset-height: 1050px;
  --asset-font-size: 2.2em;
}

.asset {
  font-family: "PT Serif";
  color: #221a0f;
  color: var(--darkest);
  background-color: #fff;
  background-image: url(/ironsworn-asset-workbench/static/media/ironsworn_asset_background.99161ece.svg);
  background-size: contain;
  box-sizing: border-box;
  font-size: 2.2em;
  font-size: var(--asset-font-size);
  height: 1050px;
  height: var(--asset-height);
  width: 750px;
  width: var(--asset-width);
  line-height: 1.3em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.top {
  text-transform: uppercase;
  font-weight: 900;
  font-family: Simonetta;
}

.type {
  color: white;
  padding: 0.8em 0 0.3em 0.95em;
  font-size: 1.03em;
}

.asset-name {
  font-size: 1.26em;
  padding: 0.7em 0.7em 0.53em 0.7em;
}

.header-circle {
  color: white;
  border: 0.19em solid transparent;
  border-radius: 50%;
  width: 4em;
  height: 4em;
  float: right;
  margin-top: -1.8em;
  margin-right: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-icon {
  font-size: 2.5em;
}

.details {
  margin-left: 0.9em;
  margin-right: 0.7em;
  font-size: 0.97em;
}

.write-in {
  border-bottom: 0.14em solid #414042;
  border-bottom: 0.14em solid var(--gray);
  margin-right: 0.2em;
  margin-bottom: 0.71em;
}

.write-in::after {
  content: ":";
}

.description {
  margin-bottom: 0.28em;
}

.ability {
  display: flex;
  line-height: 1.36em;
}

.ability-name {
  font-weight: bold;
}

.ability-name::after {
  content: ": ";
}

.dot {
  margin: -0.06em 0.27em 0 -0.03em;
  font-size: 1.58em;
}

.filled::before {
  content: "●";
}

.unfilled::before {
  content: "○";
}

li {
  list-style-position: outside;
  margin-left: 1em;
}

.track {
  display: flex;
  justify-content: center;
  margin-bottom: 0.85em;
  font-size: 1.42em;
  font-family: Simonetta;
  color: #414042;
  color: var(--gray);
  width: 91.5%;
  align-self: center;
}

.value.text {
  padding: 0.7em;
  text-align: center;
}

.value {
  width: 100%;
  height: 1.7em;
  border: 0.1em solid #414042;
  border: 0.1em solid var(--gray);
  border-left: 0.05em solid #414042;
  border-left: 0.05em solid var(--gray);
  font-weight: bolder;
  display: flex;
  justify-content: center;
  align-items: center;
}

.value svg {
  width: 100%;
  height: auto;
}

.value:first-child {
  border-left: 0.1em solid #414042;
  border-left: 0.1em solid var(--gray);
}

.asset.two-thirds {
  height: calc(1050px * (2 / 3));
  height: calc(var(--asset-height) * (2 / 3));
  width: calc(750px * (2 / 3));
  width: calc(var(--asset-width) * (2 / 3));
  font-size: calc(2.2em * (2 / 3));
  font-size: calc(var(--asset-font-size) * (2 / 3));
}

.asset.one-half {
  height: calc(1050px / 2);
  height: calc(var(--asset-height) / 2);
  width: calc(750px / 2);
  width: calc(var(--asset-width) / 2);
  font-size: calc(2.2em / 2);
  font-size: calc(var(--asset-font-size) / 2);
}

.asset.one-third {
  height: calc(1050px / 3);
  height: calc(var(--asset-height) / 3);
  width: calc(750px / 3);
  width: calc(var(--asset-width) / 3);
  font-size: calc(2.2em / 3);
  font-size: calc(var(--asset-font-size) / 3);
}

.print-page {
  /* paper-css sheet styles */
  margin: 0;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  page-break-after: always;

  /* dimensions */
  width: 8.5in;
  height: 11in;
  padding: 0.25in 0.5in;

  /* my styles */
}

.print-page img {
  max-width: 100%;
  max-height: 100%;
}

/* Hiding everything but what I want */
@media print {
  .app-header,
  .footer,
  .print-controls {
    display: none;
  }
}

/** For screen preview **/
@media screen {
  .print-page {
    background: white;
    box-shadow: 0 0.5mm 2mm rgba(0, 0, 0, 0.3);
    margin: 5mm auto;
  }
}

/** Fix for Chrome issue #273306 **/
@media print {
  .print-page {
    width: 8.5in;
  }
}

